<template>
  <div id="app">
    <h2>To-Do-List</h2>
    <el-input
        autofocus="true"
        class="add-list"
        clearable
        v-model.trim="newTask"
        placeholder="What are you going to do?"
        @keyup.enter.native="addOneTodo"
    ></el-input>
    <!-- 存储列表 -->
    <div class="content">
        <div class="c-list" v-for="item in todoList" :key="item.id">
            <el-checkbox @change="changeItem(item)" class="check-box" v-model="item.isDone"></el-checkbox>
            <span :title="item.title" :class="{ done: item.isDone }" @dblclick="editOneTodo(item)" class="title">{{ item.title }}</span>
            <span class="time">{{ item.date }}</span>
            <el-button @click="deletOneTodo(item)" class="delet-btn" type="danger" icon="el-icon-delete" circle size="mini"></el-button>
            <el-input
                v-focus
                autofocus
                clearable
                v-show="item.isEditing"
                v-model="editingItem.title"
                @keyup.enter.native="doneEdit(item)"
                @keyup.esc.native="cancelEdit(item)"
                @blur="blurEdit(item)"
                class="edit-list"
            ></el-input>
        </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "app",
  data() {
      return {
          todoList: [], // 所有备忘录
          newTask: "", // 新增的备忘记录
          editingItem: {}, // 修改中的备忘记录
      }
  },
  mounted() {
      let localList = localStorage.getItem("todo-list");
      if (localList) {
          this.todoList = JSON.parse(localList);
      } else {
          this.todoList.push({
              id: Date.now(),
              title: "双击编辑任务，回车创建任务",
              isDone: true,
              isEditing: false,
              date: new Date().toLocaleString()
          })
      }
  },
  methods: {
      // 添加一个todo
      addOneTodo() {
          if (!this.newTask) {
              this.$notify({
                  title: '标题不能为空',
                  type: 'warning',
                  showClose: false
              });
              return;
          }
          this.todoList.unshift({
              id: Date.now(),
              title: this.newTask,
              isDone: false,
              isEditing: false,
              date: new Date().toLocaleString()
          })
          this.newTask = "";
          this.setHistory();
      },
      // 编辑一个todo
      editOneTodo(todo) {
          this.todoList.forEach(item => {
              item.isEditing = false;
          });
          let index = this.todoList.findIndex( item=>{
              return item.id === todo.id
          })
          this.todoList[index].isEditing = true;
          this.editingItem = {...todo}
          this.setHistory();
      },
      // 删除一个todo
      deletOneTodo(todo) {
          let index = this.todoList.findIndex( item=>{
              return item.id === todo.id
          })
          this.todoList.splice(index, 1)
          this.setHistory();
      },
      // 确认编辑
      doneEdit(todo) {
          let index = this.todoList.findIndex( item=>{
              return item.id === todo.id
          })
          if (!this.editingItem.title) {
              this.todoList[index].isEditing = false;
              this.editingItem = {};
              return;
          }
          this.editingItem.date = new Date().toLocaleString();
          this.todoList.splice(index, 1, this.editingItem)
          this.todoList[index].isEditing = false;
          this.editingItem = {};
          this.setHistory();
      },
      // 取消编辑
      cancelEdit(todo) {
          let index = this.todoList.findIndex( item=>{
              return item.id === todo.id
          })
          this.todoList[index].isEditing = false;
          this.editingItem = {};
          this.setHistory();
      },
      // 失去焦点
      blurEdit(todo) {
          let index = this.todoList.findIndex( item=>{
              return item.id === todo.id
          })
          this.todoList[index].isEditing = false;
          this.setHistory();
      },
      // 改变了完成状态
      changeItem(todo) {
          let index = this.todoList.findIndex( item=>{
              return item.id === todo.id
          })
          this.todoList[index].isDone = todo.isDone;
          this.setHistory();
      },
      // 设置到本地缓存中
      setHistory() {
          localStorage.setItem("todo-list", JSON.stringify(this.todoList))
      }
  },
}
</script>
