    position: relative;
    height: 40px;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-around;
    align-items: center;
    box-shadow: 0 2px 4px rgb(0 0 0 / 12%), 0 0 6px rgb(0 0 0 / 4%);
}
.check-box, .delet-btn {
    margin: 0 10px 0 10px !important;
}
.title {
    flex: 1;
    display: inline-block;
    height: 40px;
    line-height: 30px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-weight: bold;
    text-align: left;
}
.time {
    font-size: 12px;
    transform: scale(0.8);
    color: darkgrey;
    position: absolute;
    display: inline-block;
    height: 14px;
    line-height: 14px;
    left: 22px;
    bottom: 0px;
}
.done {
    font-style: oblique;
    font-weight: normal;
    color: gray;
}
